<template>
    <div id="operate">
        <button @click="Operation(0)" class="btn-sort">点击排序</button>
        <button @click="Operation(1)" class="btn-sort">自动排序</button>
        <button @click="Operation(2)" class="btn-sort">重新开始</button>
        <button @click="Operation(3)" class="btn-sort" ref="state">{{ flag ? '暂停' : "继续" }}</button>
        <textarea name="inputBox" id="inputBox" cols="30" rows="10" placeholder="请输入数组，不同数字以空格分隔" ref="inputBox"
            v-model="arrNums"></textarea>
    </div>
</template>

<script>
export default {
    data() {
        return {
            arrNums: [],
            flag: true
        }
    },
    methods: {

        Operation(mode) {            //mode 0下一步  1 自动
            switch (mode) {
                case 0:              //下一步
                    this.$bus.$emit('next')
                    break
                case 1:              //自动排序
                    //this.$bus.$emit('ChangeSortMethods','bubble')           //默认冒泡排序
                    this.$bus.$emit('Sort')
                    break
                case 2:              //重新开始
                    this.$bus.$emit('Restart', this.$refs.inputBox.value.split(' '))
                    break
                case 3:
                    this.ChangeState()
                    this.$bus.$emit('SortState')
                    break
            }
        },
        ChangeState() {
            this.flag = !this.flag
        }
    },
    watch: {
        immediate: true,
        arrNums: {
            handler(newValString) {
                this.$bus.$emit('getNewArr', newValString.split(' '))
            }
        }
    },

}
</script>

<style scoped>
#operate {
    width: 70%;
    height: 100px;
    background-color: transparent;
    position: absolute;
    right: 30px;
    bottom: 2%;
    border-radius: 5px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}

textarea {
    width: 400px;
    height: 99px;
    border-radius: 5px;
    resize: none;
    border: none;
    outline: none;
}

textarea::placeholder {
    padding: 10px;
    margin-left: 10px;
}

button{
    width: 80px;
    height: 50px;
    margin-top: 20px;
    border-radius: 5px;
    font-size: 15px;
    background-color: rgba(234, 220, 220, 0.1);
    color: #fff;
    transition: all .5s;
    cursor: pointer;
    border: none;
}

button:hover{
    transform: scale(1.5);
    background-color: rgb(253, 253, 253,.5)
}
</style>