<template>
  <div id="app">
    <!-- <MyCanvas /> -->
    <MyHeader />
    <MyList :arr="arr"/>
    <MyOperate />
    <MyNav />
  </div>
</template>

<script>
// import MyCanvas from './components/MyCanvas.vue'
import MyHeader from './components/MyHeader.vue'
import MyList from './components/MyList.vue'
import MyOperate from './components/MyOperate.vue'
import MyNav from './components/MyNav.vue'
import { init } from 'events'

export default {
  name: 'App',
  data(){
    return {
      arrHis:[],
      arr:[],
      speed:2500,
      initFlag:false,
      timer:0,
      sortMethods:'',
      iteratorTemp:'',
      iterator:''    //迭代器设置在data里面，对methods全局使用
    }
  },
  components: {
    MyHeader,MyList,MyOperate,MyNav
  },
  methods:{
    /**************************************** */ 
    async  Wait(time){
    let sleep2= (time)=> new Promise((resolve)=>{
          setTimeout(resolve,time)
      })
      sleep2(2000)
      await sleep2(time);
      fun();
    },

    /******************************************** */
    //排序动画
    next(){
      this.iterator.next()
    },
    Sort(){
      if(!this.timer){     
          this.timer= setInterval(()=>{
          let val=this.iterator.next()
          if(val.done){
            clearInterval(this.timer)
            this.timer=0                  //定时器不存在
          }
        },this.speed)
      }
    },

    //冒泡排序(生成器函数分布执行)
    * BubbleSort(){
      for (let i = 0; i < this.arr.length-1; i++) {
        for (let j = 0; j <this.arr.length-i-1; j++) {
          this.$bus.$emit('changeColor',j,j+1)          //传递信息（正在比较的两个元素）
          yield 0
          if (this.arr[j + 1] < this.arr[j]) {
            let temp = this.arr[j]
            this.$set(this.arr,j,this.arr[j + 1])        //使用 set 为数组元素添加监视
            this.$set(this.arr,j+1,temp)
            yield 1                                     //第二次的 yield 使两个已经标记的元素再次下一步后才交换
          }
        }
        // this.$bus.$emit('addDoneClass',this.arr.length-i-1,0)
      }
      //  this.$bus.$emit('addDoneClass',0,1)
    },
    //选择排序
    * selectSort(){
      for (let i = 0; i < this.arr.length; i++) {
        let minIndex=i
        this.$bus.$emit('addDoneClass',-1,-1,minIndex)
        for (let j = i+1; j <this.arr.length; j++) {
          console.log(this.arr[minIndex])
          this.$bus.$emit('changeColor',i,j) 
          if (this.arr[j] <= this.arr[minIndex]) {
            minIndex=j
            this.$bus.$emit('addDoneClass',-1,-1,minIndex)                                  
          }        
          yield                                        
        }
        let temp = this.arr[minIndex]
        this.$set(this.arr,minIndex,this.arr[i])        
        this.$set(this.arr,i,temp)
        yield   
        // this.$bus.$emit('addDoneClass',i,0)
      }
      // this.$bus.$emit('addDoneClass',0,1)
    },
    //直接插入排序
    * insert(){
      console.log('insert')
      for(let i=0;i<this.arr.length;i++){
        let temp=this.arr[i],j=i-1;
        this.$bus.$emit('addDoneClass',-1,-1,i)
        this.$bus.$emit('changeColor',i,i)
        yield
        while(j>=0&&this.arr[j]>temp){
          this.$set(this.arr,j+1,this.arr[j])
          this.$set(this.arr,j,temp)
          this.$bus.$emit('addDoneClass',-1,-1,j)
          // this.$bus.$emit('changeColor',j+1,j)
          yield
          j--
        }
      }
    },
    //希尔排序
    * shell(){
      console.log('shell')  
      let div1=document.querySelectorAll('.base')
      for (let gap = Math.floor(this.arr.length/2);gap >= 1; gap =Math.floor(gap/2)) {  //外层循环用来分组--步长
        for (let i = gap;i < this.arr.length;i ++) {
          let temp = this.arr[i],j=i-gap;
          for (j = i;j >= 0;j = j - gap) 
                div1[j].classList.add('div-sorting')       
          for (j = i-gap;j >= 0 &&temp < this.arr[j] ;j = j - gap) {
            this.$set(this.arr,j+gap,this.arr[j])
            this.$set(this.arr,j,temp)
            yield
          }
          for (j = i;j >= 0;j = j - gap) 
            div1[j].classList.remove('div-sorting')       
        }
      }
    },
    //快速排序
    //更新数组
    getNewArr(newArr){   
      this.arr=newArr.filter(ele=> ele!='').map(Number)       //剔除字符串数组中的空串之后，将字符串数组转化为数字数组
      this.initFlag=true
    },
    //排序状态
    SortState(){
      if(this.timer){            //若存在定时器，则清除，达到暂停效果
        this.iteratorTemp=this.iterator  //保存已经排序过的迭代器
        clearInterval(this.timer)
        this.timer=0
      }else{
        this.iterator=this.iteratorTemp
        this.Sort()
      }
    },
    //更改排序方式(获取迭代器)
    ChangeSortMethods(methods){
      this.timer=0
      switch(methods){
        case 'bubble':
          this.iterator=this.BubbleSort()
          break
        case 'select':
          this.iterator=this.selectSort()
          break
        case 'insert':
          this.iterator=this.insert()
          break
        case 'shell':
          this.iterator=this.shell()
          default:
            // console.warn('未选择排序方式')
      }
    },
    //重新开始
    Restart(newArr){
      clearInterval(this.timer)
      this.getNewArr(newArr)
      this.$bus.$emit('initClass')
    }

  },
  watch:{
    arr:{               //监视数组， 在数组改变后做初始化工作
      immediate:true,
      handler(){
        /* console.log('初始化了')    问题：我们想要的是在重新输入数组后初始化，但是
        当数组在排序的时候arr内的顺序发生变化，被侦听到了，因此发生初始化。因此使用标志量判断是否重新执行以下初始化操作 */
        if(this.initFlag){
          this.$bus.$emit('initClass')
          this.initFlag=false
          this.timer=0
        }
      }
    },
  },
  mounted(){
    this.$bus.$on('next',this.next)
    this.$bus.$on('getNewArr',this.getNewArr)
    this.$bus.$on('Sort',this.Sort)
    this.$bus.$on('SortState',this.SortState)
    this.$bus.$on('ChangeSortMethods',this.ChangeSortMethods)
    this.$bus.$on('Restart',this.Restart)
  }

}
</script>

<style>
*{
  margin: 0;
  padding: 0;
}

#app{
  width: 100%;
  height: 100vh;
  background-color: #333;
}


</style>
