<template>

    <ul>
        <MyItem v-for="(valNum,index) in this.arr" :key="index" :valNum="valNum" :maxHeight="maxHeight"/> 
    </ul>
   
</template>

<script>
import MyItem from './MyItem.vue'

export default {
    components:{
        MyItem
    },
    data(){
        return {
            divStyle:['','div-sorting','div-sorted','div-mark'],
        }
    },
    methods:{
        changeColor(i,j){
            let div1=document.querySelectorAll('.base')
            div1[i].classList.add(this.divStyle[1])
            div1[j].classList.add(this.divStyle[1])
            /* 阴差阳错的实现了时间刚刚对点的交换动画
            先前存在的问题：
                当两个元素要交换时，transition的时间和排序定时器的时间有误差，因此还没过渡完，定时器到时间，下一次的过渡元素就
                激活了


            trsition属性放在了Item中的base上，并不再放在每次添加的class中

            1、自动排序中的定时器 X（2500） 毫秒  当changeColor时，增添属性触发了 transition（2000），当500毫秒之后触发定时
            器，删除样式又触发了transition，又开始过渡2000毫秒，所以从添加属性到删除属性的过程中，一共 2500 毫秒，和排序定时器
            吻合

            2、当两个元素要交换时，触发了Item组件中 valNUm 值的变化，因此也触发了transition，又是2000毫秒
            
            */
            setTimeout(()=>{
                div1[i].classList.remove(this.divStyle[1])
                div1[j].classList.remove(this.divStyle[1])
            },500)                      
        },
        changeColorOfShell(i,gap){
            let div1=document.querySelectorAll('.base')
            let j=0
            for (j = i-gap;j >= 0;j = j - gap) {
                div1[j].classList.add(divStyle[1])       
            }

        },
        //是否排序结束
        sortDone(){        
            for(let i=0;i<this.arr.length-1;i++){
                if(this.arr[i]>this.arr[i+1]){
                    return false
                }
            }
            return true
        },
        addDoneClass(i,mode,mark=-1){
            let div1=document.querySelectorAll('.base')
            let div2=document.querySelectorAll('.base-mark')
           
            if(mode===0){           //模式 0  数组排序还没循环完，每次只为排序序列的最后一个元素添加样式
                div1[i].classList.add(this.divStyle[2])
            }
            else if(mode===1){     //模式 1  数组排序循环完，为所有元素添加样式
                div1.forEach(ele=>{ ele.classList.add(this.divStyle[2]) })
            }

            if(mark!=-1){       //存在标记元素
                div2.forEach(ele=>{ ele.classList.remove(this.divStyle[3]) })
                div2[mark].classList.add(this.divStyle[3])
            }
        },
        initClass(){
            let div1=document.querySelectorAll('.base')
            div1.forEach(ele=>{ 
                ele.classList.remove(this.divStyle[1])
                ele.classList.remove(this.divStyle[2])
                ele.classList.remove(this.divStyle[3])
            })
        },
        //交换动画
        swapLiBox(i,j){

        }
    },
    props:['arr'],
    mounted(){
        this.$bus.$on('changeColor',this.changeColor)
        this.$bus.$on('changeColorOfShell',this.changeColorOfShell)
        this.$bus.$on('addDoneClass',this.addDoneClass)
        this.$bus.$on('initClass',this.initClass)
        // this.$bus.$on('swapLiBox',this.swapLiBox)
    },
    computed:{
        maxHeight:{                 //maxHeight属性让不同数值成等比例缩放
            get(){
                let max=this.arr[0]
                this.arr.forEach(ele=>{ max=(max<ele?ele:max)})
                return max
           }
        }
    }
}
</script>

<style>

ul{ 
    position: absolute;
    right: 30px;
    top: 20%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    width: 70%;
    height: 500px;
    background-color:rgba(238, 238, 238, .5);
    overflow: hidden;
    border-radius: 5px;
}

li{
    display: flex;
    flex-direction: column;
    justify-content:flex-end;
    list-style: none;
    margin: 20px 0 50px 70px;          /* 上 右 下 左 */ 
}

.div-sorting{
    background-color: rgb(253, 65, 8);
}

.div-sorted{
    background-color: orange;
   
}

.div-mark{ 
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0px 0px 13px 13px;
    border-color: transparent transparent red transparent;
}

</style>