<template>
    <div class="nav">
        <!-- 为父盒子绑定事件，捕获阶段触发子盒子 -->
        <div class="sort" @click="choiceSortMethods">           
            <!-- 交换排序 -->
            <!-- <div class="tag" >交换排序</div> -->
            <div class="opt"><input class="bubble" id="bubble" value="冒泡排序" type="button"></div>
            <div class="opt"><input class="quick" id="quick" value="快速排序"  type="button"></div>
            <!-- <div class="opt"><input class="quick2" id="quick2" value="快速排序Ⅱ"  type="button"></div> -->
            <!-- 插入排序 -->
            <!-- <div class="tag">插入排序</div> -->
            <div class="opt"><input class="insert" id="insert" value="插入排序"  type="button"></div>
            <div class="opt"><input class="shell" id="shell" value="希尔排序"  type="button"></div>
            <!-- 选择排序 -->
            <!-- <div class="tag">选择排序</div> -->
            <div class="opt"><input class="select" id="select" value="选择排序"  type="button"></div>
            <!-- 归并排序 -->
            <!-- <div class="tag">归并排序</div> -->
            <div class="opt"><input class="merge" id="merge" value="归并排序"  type="button"></div>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return {
            sortMethods:['bubble','quick','quick2','insert','shell','select','merge']
        }
    },
    methods:{
        choiceSortMethods(e){
            this.$bus.$emit('ChangeSortMethods',e.target.id)
        }

    },
}
</script>

<style scoped>

.nav{
    position: absolute;
    top: 10vh;
    left: 0;
    display: flex;
    flex-direction: column;
    justify-content:space-around;
    align-items:center;
    width: 300px;
    height: 90vh;
}

input{
    width: 180px;
    height: 60px;
    margin-top: 20px;
    border-radius: 5px;
    font-size: 15px;
    background-color: rgba(234, 220, 220, 0.1);
    color: #fff;
    transition: all .5s;
    cursor: pointer;
    border: none;
}

input:hover{
    transform: scale(1.5);
    background-color: rgb(253, 253, 253,.5)
}
</style>
